/*Pixels to rems*/ /* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: "Inter", sans-serif;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --orange: hsl(35, 77%, 62%);
  --Red: #f15e50;
  --White: hsl(36, 100%, 99%);
  --Grayish-blue: hsl(233, 8%, 79%);
  --Dark-blue: hsl(236, 13%, 42%);
  --Very-dark-blue: hsl(240, 100%, 5%);
}

body {
  font-family: "Inter", sans-serif;
}

.header {
  display: flex;
  width: 100%;
  align-items: center;
  padding-top: 3.75rem;
  position: relative;
}
@media (max-width: 50em) {
  .header {
    padding-top: 0;
  }
}
.header__logo img {
  width: 4.125rem;
  height: 2.5625rem;
}
@media (max-width: 50em) {
  .header__logo img {
    width: 2.9375rem;
    height: 1.875rem;
  }
}
@media (max-width: 50em) {
  .header__logo {
    position: absolute;
    top: 0;
    left: 0;
    translate: 100% 180%;
  }
}
.header__nav {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  align-self: flex-start;
}
.header__nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__nav ul li {
  list-style-type: none;
  padding: 20px;
}
.header__nav ul li a {
  text-decoration: none;
  font-size: 1rem;
  color: var(--Very-dark-blue);
  font-weight: 700;
}
.header__nav ul li a:hover {
  color: var(--orange);
}
@media (max-width: 50em) {
  .header__nav ul {
    height: 100vh;
    flex-direction: column;
    align-items: flex-start;
    background: #fff;
    width: 70%;
    margin-top: 0;
    position: absolute;
    transition: opacity 500ms ease-in-out;
  }
}
@media (max-width: 50em) {
  .header__nav .hide {
    opacity: 0;
    transition: opacity 500ms ease-in-out;
  }
}
.header__hamburger {
  position: absolute;
  top: 0;
  right: 0;
  translate: -100% 140%;
}
.header__hamburger img {
  display: none;
  width: 2.0625rem;
  height: 2.0625rem;
  cursor: pointer;
}
@media (max-width: 50em) {
  .header__hamburger img {
    display: inline-block;
  }
}

.wrapper {
  padding: 0 10.3125rem;
}
@media (max-width: 90em) {
  .wrapper {
    padding: 0;
  }
}

.container {
  display: grid;
  grid-template-columns: 1fr;
  margin: 7rem 1.125rem 1.125rem 1.125rem;
}
@media (min-width: 68.75em) {
  .container {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    margin: 1.125rem 1.125rem 1.125rem 1.125rem;
  }
}

@media (min-width: 50em) {
  .main {
    grid-column: span 1;
    grid-row: span 2;
  }
}
@media (min-width: 68.75em) {
  .main {
    grid-column: span 2;
    grid-row: span 2;
  }
}
.main__grid {
  display: grid;
  grid-template-columns: 1fr;
}
@media (min-width: 68.75em) {
  .main__grid {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
}
.main__image {
  background: url("/assets/images/image-web-3-mobile.jpg") center center/cover no-repeat;
  min-width: 21.5625rem;
  min-height: 18.875rem;
  margin-bottom: 1.875rem;
}
@media (min-width: 68.75em) {
  .main__image {
    background: url("/assets/images/image-web-3-desktop.jpg") center center/cover no-repeat;
    grid-column: span 2;
    grid-row: span 1;
  }
}
.main__image img {
  min-width: 21.5625rem;
  min-height: 18.875rem;
}
.main__title {
  margin-bottom: 1.75rem;
}
@media (min-width: 68.75em) {
  .main__title {
    grid-column: span 1;
    grid-row: 2/3;
  }
}
.main__headline {
  font-size: 1.9375rem;
  font-size: clamp(1.9375rem, 1.673415493rem + 1.1267605634vw, 2.6875rem);
  line-height: 1.1;
  font-weight: 800;
  letter-spacing: 2px;
}
@media (max-width: 50em) {
  .main__headline span::after {
    content: "\a";
    white-space: pre;
  }
}
@media (min-width: 68.75em) {
  .main__subtext {
    grid-column: 2/3;
    grid-row: 2/3;
  }
}
.main__text {
  font-size: 0.875rem;
  line-height: 1.5;
  margin-bottom: 1.75rem;
}
.main__button {
  text-align: center;
  letter-spacing: 2px;
  font-size: 0.875rem;
  background: var(--Red);
  width: 11.4375rem;
  height: 2.875rem;
  border: 2px solid #d02c20;
  color: #fff;
}
.main__button:hover {
  background: var(--Very-dark-blue);
  border: 2px solid var(--Very-dark-blue);
  cursor: pointer;
}

.new {
  padding: 1.6875rem 1.4375rem 1.6875rem 1.4375rem;
  margin: 4.0625rem 0;
  max-width: 22rem;
  max-height: 32.0625rem;
  background: var(--Very-dark-blue);
}
@media (min-width: 50em) {
  .new {
    grid-column: 3/4;
    grid-row: span 2;
    margin-top: 0;
    margin-left: 1.875rem;
  }
}
.new__title {
  font-size: 1.6875rem;
  font-size: clamp(1.6875rem, 1.5774647887rem + 0.4694835681vw, 2rem);
  color: var(--orange);
}
.new__article {
  padding-top: 2.25rem;
}
.new__article:nth-child(2), .new__article:nth-child(3) {
  border-bottom: 1px solid var(--Dark-blue);
  padding-bottom: 2.25rem;
}
.new__article a {
  display: block;
  font-size: 1.125rem;
  font-weight: 800;
  color: var(--White);
  text-decoration: none;
  margin-bottom: 1rem;
}
.new__article a:hover {
  color: var(--orange);
}
.new__text {
  margin-right: 1.5625rem;
  font-size: 0.8125rem;
  color: var(--Dark-blue);
}

.article {
  display: flex;
  flex-direction: column;
}
@media (min-width: 50em) {
  .article {
    flex-direction: row;
    margin-top: 1.875rem;
    grid-column: 1/4;
    grid-row: 3/4;
  }
}
.article__wrapper {
  display: flex;
  margin-bottom: 2.125rem;
}
.article__image {
  margin-right: 1.5rem;
}
.article__image-img {
  max-width: 6.375rem;
  max-height: 8rem;
}
.article__number {
  font-size: 1.5625rem;
  color: var(--Grayish-blue);
}
.article__title {
  color: var(--Very-dark-blue);
  text-decoration: none;
  font-size: 1rem;
  font-weight: 800;
}
.article__title:hover {
  color: var(--Red);
}
.article__text {
  font-size: 0.8125rem;
  color: var(--Very-dark-blue);
  margin-right: 2.5rem;
}